            wrap
            class="mt-1 mb-1 align-content-space-between">
    <v-flex v-for="(sensor, i) in node"
            xs12
            sm6
            md3
            class="border px-1"
            :key="i">
      <sensor-box :sensorType="sensor.type"
                  :sensorName="sensor.name"
                  :good="checkStatus(sensor.ideal, parseInt(sensor.data))"
                  :data="sensor.data"
                  :ideal="sensor.ideal">
      </sensor-box>
    </v-flex>
  </v-layout>
  <!-- {{nodeData.time}} -->
  <DashboardChart :nodeData="nodeData"
                  :selectedNode="selectedNode"></DashboardChart>
  <!-- {{getLastestReading}} -->
  <v-layout row
            wrap>
    <v-flex xs12
            md12
            class="px-1">
      <RecentNotificationBox :nodeId="getSelectedNodeId.toString()"></RecentNotificationBox>
    </v-flex>
