          <v-list>
            {{nodeData}}
            {{Object.keys(getAllNodeData)}} {{getAllNodeId}}
          </v-list>
        </v-card-text>
      </v-card>
    </v-flex> -->
  </v-layout>
</div>
</template>
<script>
import SensorBox from '@/components/SensorBox.vue'
import DashboardChart from '@/components/DashboardChart.vue'
import SensorAvatar from '@/components/SensorAvatar.vue'
import RecentNotificationBox from '@/components/RecentNotificationBox.vue'
// import { server } from '@/js/axios.js'
import {
  mapGetters,
  mapActions
} from 'vuex'

export default {
  name: 'Dashboard',
  components: { SensorBox, DashboardChart, SensorAvatar, RecentNotificationBox },
  data() {
    return {
      msg: 'Dashboard',
      node: [
        { type: 'temperature', name: 'temperature', good: false, data: -1, ideal: [0, 0] },
        { type: 'humidity', name: 'humidity', good: false, data: -1, ideal: [0, 0] },
        { type: 'light', name: 'sunlight', good: false, data: -1, ideal: [0, 0] },
        { type: 'moisture', name: 'moisture', good: false, data: -1, ideal: [0, 0] }
      ],
      nodeData: { id: 'no_data_found', moisture: [], sunlight: [], temperature: [], humidity: [], time: [] }
    }
  },
  created() {},
  mounted() { if (this.getUserData) this.init() },
  watch: {
    getUserData() {
      this.init()
      //   if (this.getSelectedNodeId === 'no_node_selected') {
      //     if (process.env.NODE_ENV !== 'production') console.log('replace route')
      //     this.$router.replace({ name: 'dashboard', params: { nodeId: this.getSelectedNodeId } })
      //   } else {
      //     this.setAsyncSelectedNodeId(this.getSelectedNodeId)
      //   }
    },
    getSelectedNodeId(val, oldVal) {
      //   console.log('getSelectedNodeId change')
      if (val !== oldVal) {
        // console.log('INIT from getSelectedNodeId')
        this.init()
      }
    },
    getAllNodeData(val) { this.init() },
    nodeData() { this.setTopBoxData() }
  },
  computed: {
    ...mapGetters(['getSelectedNodeId', 'getToken', 'getMini', 'getDrawer', 'getActivePage', 'getTooltipColor']),
    ...mapGetters('user', ['getUserData']),
    ...mapGetters('user/node', ['getNodeInfoById', 'getAllSensors', 'getSensorOptions', 'getAllNodeData', 'getNodeDataById', 'getAllNodeId', 'getLastestReading']),
    selectedNode() { return this.getNodeInfoById(this.getSelectedNodeId) },
    pageActive() { return this.getActivePage === 'dashboard' }
  },
  methods: {
    ...mapActions(['setAsyncSelectedNodeId', 'updateAsyncSnackBar']),
    ...mapActions('user/node', ['setAsyncNodeData']),
    init() {
      if (process.env.NODE_ENV !== 'production') console.log('INIT')
      if (this.getSelectedNodeId === 'no_node_selected') { return } else {
        if (this.selectedNode.name !== 'no_node_found') this.setNodeData()
        else {
          this.setNodeData()
          this.updateAsyncSnackBar({ show: true, content: `Cannot find node with id-${this.getSelectedNodeId}`, colorType: 'error' })
        }
      }
    },
    // updateNotification(notis, dismissed) { // noti is an array
    //   const ids = []
    //   for (let n of notis) { if (n.dismissed !== dismissed) ids.push(n.id) }
    //   let count = 0
    //   while (count <= ids.length) {
    //     if (ids.slice(count, count + 20).length === 0) break
    //     this.sentTimeoutUpdateNotification(ids, count, dismissed)
    //     count += 10
    //   }
    // },
    setNodeData() {
      const id = this.getSelectedNodeId
      if (!(id in this.getAllNodeData)) { // Node data is not found in cache, load it
        this.setAsyncNodeData({ id: id, hour: 30 * 24 })
        this.nodeData = { id: 'loading_data', moisture: [], sunlight: [], temperature: [], humidity: [], time: [] }
      } else if (this.nodeData.id === 'loading_data') { // if this is waiting on loading, set it to the data in cache
        this.nodeData = this.getAllNodeData[id]
      } else {
        this.nodeData = this.getAllNodeData[id]
      }
    },
    setTopBoxData() {
      const selectedNode = this.selectedNode
      this.node = [{
        type: 'temperature',
        name: 'temperature',
        good: false,
        data: this.getLastDataPoint('temperature'),
        ideal: [selectedNode.tempMin, selectedNode.tempMax]
      }, {
        type: 'humidity',
        name: 'humidity',
        good: false,
        data: this.getLastDataPoint('humidity'),
        ideal: [selectedNode.humidityMin, selectedNode.humidityMax]
      }, {
        type: 'light',
        name: 'sunlight',
        good: false,
        data: this.getLastDataPoint('sunlight'),
        ideal: [selectedNode.sunlightMin, selectedNode.sunlightMax]
      }, {
        type: 'moisture',
        name: 'moisture',
        good: false,
        data: this.getLastDataPoint('moisture'),
        ideal: [selectedNode.moistureMin, selectedNode.moistureMax]
      }]
    },
    getLastDataPoint(type) {
      if (this.nodeData[type].length !== 0) return parseInt(this.nodeData[type][this.nodeData[type].length - 1].y)
      else if (this.nodeData.id === 'loading_data') return (-2)
      else return (-1)
    },
    parseIdealRangeHtml(range, type) {
      const celsius = '&#8451;'
      //   const fahrenheit = '&#8457;'
      const percentage = () => { return range[0] + '-' + range[1] + '%' }
      const objLiteral = {
        Humidity() { return percentage() },
        Light() { return percentage() },
        Temperature() { return range[0] + '-' + range[1] + celsius },
        Moisture() { return percentage() }
      }
      return objLiteral[type]()
    },
    checkStatus(range, value) {
      if (range[0] <= value && range[1] >= value) return true
      return false
    },
    getSensorIdeal(sensorType, maxMinType) {
      for (let sensor of this.node) {
        if (sensorType === sensor.name) {
          if (maxMinType === 'max') return sensor.ideal[1]
          else return sensor.ideal[0]
        }
      }
    }
  }
}
</script>
