<template>
<v-card class="white lighten-5 border mb-1">
  <v-card-text class="sensorBox border pt-1 px-0">
    <v-layout row
              wrap
              class="border">
      <v-flex xs12
              class="border text-xs-left subheading pa-0">
        <v-layout row
                  wrap
                  class="border">
          <v-flex xs6
                  class="border px-2">
            {{capitalizeSensorType}}
          </v-flex>
          <v-flex xs6>
            <v-layout row
                      wrap
                      class="text-xs-right">
              <v-flex xs4
                      class="border icon"></v-flex>
              <v-flex xs4
                      class="border icon"></v-flex>
              <v-flex xs4
                      class="border px-2">
                <v-icon class="border"
                        style="font-size: 20px;"
                        :class="{'pink--text': !good, 'light-blue--text': good}">
                  radio_button_checked
                </v-icon>
              </v-flex>
            </v-layout>
          </v-flex>
        </v-layout>
      </v-flex>
      <v-flex xs12
              class="border display-1 grey--text text-xs-center headline">
        <span v-if="data == -1">
          No Data
        </span>
        <span v-else-if="data == -2">
          <v-progress-circular indeterminate
                               v-bind:size="28"
                               :color="getSensorOptions[sensorName].colorName"></v-progress-circular>
        </span>
        <span v-else>
          <i-odometer class="iOdometer"
                      :value="data"></i-odometer>
          <span v-html="unit"
                style="position:relative; top: 4px;"></span>
        </span>
      </v-flex>
      <v-flex xs12
              class="border pa-0">
        <div class="infoBox pa-0 grey lighten-5 border">
          <v-layout row
                    wrap
                    class="border innerInfoBox pa-0"
                    :class="{'pink--text': !good, 'light-blue--text': good}">
            <v-flex xs12
                    class="subheading text-xs-left border pa-0 ml-4"
                    style="font-weight: 600">Ideal</v-flex>
            <v-flex xs12
                    class="title text-xs-center border"
                    style="font-weight: 600">
              <i-odometer class="iOdometer"
                          :value="ideal[0]"></i-odometer>
              -
              <i-odometer class="iOdometer"
                          :value="ideal[1]"></i-odometer>
              <span v-html="unit"
                    style="position:relative; top: 2px;"></span>
            </v-flex>
          </v-layout>
        </div>
      </v-flex>
    </v-layout>
  </v-card-text>
</v-card>
</template>

<script>
import IOdometer from 'vue-odometer'
import { mapGetters } from 'vuex'

export default {
  name: 'sensorBox',
  components: {
    IOdometer
  },
  props: {
    sensorType: {
      type: String,
      default: 'Loading'
    },
    sensorName: {
      type: String,
      default: 'Loading'
    },
    good: {
      type: Boolean,
      default: true
    },
    data: {
      type: Number,
      default: '(%|&#8451;|&#8457;)'
    },
    ideal: {
      type: Array,
      default: [0, 0]
    }
  },
  data() {
    return {
      msg: 'Template or Testing page'
    }
  },
  created() {
    // console.log(this._.random(20))
  },
  watch: {},
  computed: {
    ...mapGetters('user/node', ['getSensorOptions']),
    unit() {
      if (this.sensorType === 'temperature') return '&#8451;'
      return '%'
    },
    capitalizeSensorType() {
      return this.sensorType[0].toUpperCase() + this.sensorType.slice(1)
    }
  },
  methods: {}
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style scoped lang="sass">
.border
  // border: 1px dashed grey

.infoBox
  cursor: default
  height: 55px
  margin: 0 auto
  width: 98%
  .innerInfoBox
    transition: all 0.1s ease

.icon
  padding-left: 0
  transition: all 0.1s ease
  cursor: default
  &:hover
    color: #bEbEbE

.nodeId
  cursor: default
  font-weight: 600
  color: #bEbEbE

.sensorBox
  cursor: default
  height: 120px
  box-shadow: 0px 1px 1px #BBBBBB
  transition: all 0.5s ease
  &:hover
    box-shadow: 3px 3px 4px #888888

</style>



